import { BrowserRouter, Route } from 'react-router-dom';

import './App.css';
import { LangState } from './context/lang';
import Header from './Header/Header';
import MainPageSlider from './MainPageSlider/MainPageSlider';
import NewsSection from './NewsSection/NewsSection';
import About from './About/About';
import MainContacts from './MainContacts/MainContacts';
import AllContacts from './AllContacts/AllContacts';
import Sisterhood from './Sisterhood/Sisterhood';
import Rector from './Rector/Rector';
import Parish from './Parish/Parish';
import OrthodoxFaith from './OrthodoxFaith/OrthodoxFaith';
import Footer from './Footer/Footer';
import ScheduleLiturgy from './ScheduleLiturgy/ScheduleLiturgy';

function App() {
  return (
    <BrowserRouter basename='/holy-trinity-orthodox-church-build'>
      <LangState>
        <Header />
        <Route path='/' exact>
          <MainPageSlider />
          <NewsSection />
          <About />
          <MainContacts />
        </Route>
        <Route path='/parish'>
          <Parish />
        </Route>
        <Route path='/rector' exact>
          <Rector />
        </Route>
        <Route path='/sisterhood' exact>
          <Sisterhood />
        </Route>
        <Route path='/schedule' exact>
          <ScheduleLiturgy />
        </Route>
        <Route path='/orthodox-faith'>
          <OrthodoxFaith />
        </Route>
        <Route path='/contacts' exact>
          <AllContacts />
        </Route>
        <Footer />
      </LangState>
    </BrowserRouter>
  );
}

export default App;